.PhoneView {
  width: calc(100% - 40px);
  height: calc(100% - 105px);
  position: absolute;
  top: 65px;
  display: grid;
  align-items: center;
  justify-items: center;
  grid-template-rows: 150px 1fr 80px;
  padding: 20px;
}

.SuccessOtherView {
  grid-template-rows: 250px 1fr 80px;
}

.contenidoView,
.controlesView {
  width: 100%;
  height: 100%;
}

.contenidoView {
  display: grid;
  justify-items: center;
  position: relative;
}

.successMessage {
  width: 100%;
  height: 100%;
  position: absolute;
  background: white;
  z-index: 1;
  display: grid;
  align-items: center;
  justify-items: center;
}

#successOperation {
  justify-self: center;
  margin: 0 !important;
  font-size: 22px;
  color: #59b200;
}

.contenidoView .labelText {
  font-family: fa5-proxima-nova, "Helvetica Neue", Helvetica, Arial, sans-serif;
  margin: 0 0 15px 0 !important;
  font-size: 17px;
}

.desaparece {
  -webkit-transition: 0.3s;
  transition: 0.3s;
  -webkit-transform: translateX(15px);
          transform: translateX(15px);
  opacity: 0;
}

.phoneView .containerInputComponent {
  margin-top: 4vh;
  height: 140px;
  grid-template-rows: 30px 1fr 30px;
}

.PassView .containerInputComponent {
  margin-top: 0;
  grid-template-rows: 1fr;
  height: 70px;
}

.PassView {
  width: calc(100% - 40px);
  height: calc(100% - 105px);
  position: absolute;
  top: 65px;
  display: grid;
  align-items: center;
  justify-items: center;
  grid-template-rows: 200px 1fr 80px;
  padding: 20px;
}

.MVList {
  width: calc(100% - 40px);
  height: calc(100% - 105px);
  position: absolute;
  top: 65px;
  display: grid;
  align-items: center;
  justify-items: center;
  grid-template-rows: 1fr 80px;
  padding: 20px;
}

