#authReq {
  position: absolute;
  width: 100%;
  height: 100%;
  background: #ffffffc4;
  z-index: 2;
  display: grid;
  justify-items: center;
}

.contListItemMV {
  width: 100%;
  height: 100%;
  max-height: 350px;
  overflow-y: scroll;
  align-self: flex-start;
  padding-top: 60px;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(auto-fill, 82px);
  grid-row-gap: 10px;
}

.ItemlistViewart.itemActiveListView {
  grid-template-columns: 1fr;
  background: -webkit-gradient(linear, right top, left bottom, from(#2b3742), to(#060809));
  background: linear-gradient(to bottom left, #2b3742, #060809);
  border-radius: 6px;
  border: 1px solid #38ef7d;
}
.ItemlistViewart.itemActiveListView:hover {
  grid-template-columns: 1fr;
  background: -webkit-gradient(linear, right top, left bottom, from(#2b3742), to(#060809));
  background: linear-gradient(to bottom left, #2b3742, #060809);
  border-radius: 6px;
  border: 1px solid #38ef7d;
}

.ItemlistViewart.itemActiveListView span {
  color: #38ef7d;
}

.ItemlistViewart {
  width: calc(100% - 62px);
  height: 100%;
  display: grid;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  /*grid-template-columns: 60px 1fr;*/
  padding: 0 30px;
}

.ItemlistViewart:hover {
  background: #f9f9f9;
  /*border: 1px solid #38ef7d;*/
  border-radius: 6px;
}

.ItemlistViewart span {
  margin: 0 !important;
  text-align: center;
}

.centerItem {
  display: grid;
  justify-items: center;
  align-items: center;
}

.contListItemMV::-webkit-scrollbar {
  width: 0px;
}
.contListItemMV::-webkit-scrollbar-thumb {
  background: #475a6836;
}

.findbar {
  position: absolute;
  width: 100%;
  height: 60px;
  background: #efefef;
  top: 0;
  left: 0px;
}

.findbar .containerInputComponent {
  height: auto !important;
}

.contListItemMV.ultimate {
  max-height: 65vh;
}

